<template>
    <v-layout>
        <v-card contextual-style="dark">
            <span slot="header">
                {{ $t('general.welcome') }}
            </span>
            <div slot="body">
                <button v-if="recording" @click="stopRecording">
                    StopRecording
                </button>
                <button v-else @click="startRecording">Record voices</button>

                <p>step: {{ step }}</p>
                <ul class="recList" style="height: 400px; overflow: scroll">
                    <li
                        v-for="i in recList"
                        :key="i.name"
                        style="
                            display: block;
                            height: 55px;
                            list-style: none;
                            line-height: 55px;
                        "
                    >
                        <span style="display: inline-block; width: 200px"
                            >{{ i.name }}.wav</span
                        >
                        <audio controls style="height: 50px">
                            <source :src="i.url" type="audio/m4a" />
                            <source :src="i.url" type="audio/mpeg" />
                            您的浏览器不支持 audio 元素。
                        </audio>
                    </li>
                </ul>
            </div>
            <div slot="footer">Make'em better!</div>
        </v-card>
    </v-layout>
</template>

<script>
/* ============
 * Home Index Page
 * ============
 *
 * The home index page.
 */

import VLayout from '@/layouts/Default.vue'
import VCard from '@/components/Card.vue'

export default {
    /**
     * The name of the page.
     */
    name: 'HomeIndex',

    /**
     * The components that the page can use.
     */
    components: {
        VLayout,
        VCard,
    },
    data() {
        return {
            step: 0,
            loop: true,
            recList: [
                {
                    url: '/assets/stylus/rec.m4a',
                    name: Date.now(),
                },
            ],
            recording: true,
            recordingHandler: 0,
        }
    },
    methods: {
        fetchStep: function () {
            const z = this
            return z.$http
                .get('/api/step')
                .then(function (myJson) {
                    z.step = myJson.step || 1
                })
        },
        timer(timeout) {
            const z = this
            const t = timeout || 3000
            setTimeout(function () {
                if (z.loop) {
                    z.fetchStep().then(z.timer.bind(z))
                }
            }, t)
        },
        stopRecording() {
            this.recording = false
            clearInterval(this.recordingHandler)
        },
        startRecording() {
            const z = this;
            const item = z.recList[0]
            z.recording = true
            z.recordingHandler = setInterval(function () {
                z.recList.push({
                    url: item.url,
                    name: `rec_${Date.now()}`,
                })
            }, 3500)
        },
    },
    created() {
        const z = this
        z.timer(0)
        z.startRecording();
    },
}
</script>
